import { I_CommentScene, I_Scene } from '@containers/Home/types';
import { Typography, Tabs } from 'antd';
import dynamic from 'next/dynamic';
import { memo, useCallback } from 'react';
import { ListCommentWrapper } from './styled';

const { TabPane } = Tabs;
const { Text } = Typography;

const SelectListScene = dynamic(() => import('./SelectListScene'));
const CommentSceneItem = dynamic(
  () => import('./CommentScene/CommentSceneItem'),
);

interface I_List {
  list: I_CommentScene[];
  onMoveToCommentScene: (data) => void;
  onUpdateCommentScene: (data) => void;
  onDeleteCommentScene: (data) => void;
  onSelectCommentScene: (item: I_CommentScene) => void;
}

const List = memo(
  ({
    list,
    onMoveToCommentScene,
    onSelectCommentScene,
    onUpdateCommentScene,
    onDeleteCommentScene,
  }: I_List) => {
    return (
      <div className="--list hide-scrollbar">
        {list.map(item => (
          <div key={item.id} className="--item">
            <div className="stt">{item?.index || 0}</div>
            <CommentSceneItem
              commentScene={item}
              onClick={onMoveToCommentScene}
              onUpdate={onUpdateCommentScene}
              onDelete={onDeleteCommentScene}
            >
              <small
                className="countComment"
                onClick={e => {
                  e.stopPropagation();
                  onSelectCommentScene(item);
                }}
              >
                <i aria-hidden className="fas fa-comment-alt" />
                <span>{item?.children?.length || 0} Replies</span>
              </small>
            </CommentSceneItem>
          </div>
        ))}
      </div>
    );
  },
);

interface Props {
  onBack: () => void;
  listScene: I_Scene[];
  titleProject: string;
  sceneSelected?: I_Scene;
  onNextScene: (scene: I_Scene) => void;
  onMoveToCommentScene: (commentScene: I_CommentScene) => void;
  onSelectCommentScene: (commentScene: I_CommentScene) => void;
  listCommentSceneGroupByParentActive: I_CommentScene[];
  listCommentSceneGroupByParentResolved: I_CommentScene[];
  onUpdateCommentScene: (data: I_CommentScene) => void;
  onDeleteCommentScene: (data: I_CommentScene) => void;
}

const ListComment = memo(
  ({
    onBack,
    listScene,
    onNextScene,
    titleProject,
    sceneSelected,
    onMoveToCommentScene,
    onSelectCommentScene,
    onUpdateCommentScene,
    onDeleteCommentScene,
    listCommentSceneGroupByParentActive,
    listCommentSceneGroupByParentResolved,
  }: Props) => {
    const onChangeScene = useCallback(
      id => {
        const scene = listScene.find(item => item.id === id);
        scene && onNextScene(scene);
      },
      [listScene, onNextScene],
    );

    return (
      <ListCommentWrapper>
        <div className="--header">
          <div className="--left">
            <i aria-hidden className="far fa-arrow-left" onClick={onBack} />
          </div>
          <div className="--right">
            <div className="--title">
              <Text
                style={{ width: '100%' }}
                ellipsis={{ tooltip: titleProject }}
              >
                {titleProject}
              </Text>
            </div>
            <div className="--select">
              <SelectListScene
                listScene={listScene}
                value={sceneSelected?.id || ''}
                onChange={onChangeScene}
              />
            </div>
          </div>
        </div>
        <div className="--body">
          <Tabs centered defaultActiveKey="1">
            <TabPane
              tab={`Active (${listCommentSceneGroupByParentActive.length})`}
              key="1"
            >
              <List
                list={listCommentSceneGroupByParentActive}
                onMoveToCommentScene={onMoveToCommentScene}
                onSelectCommentScene={onSelectCommentScene}
                onUpdateCommentScene={onUpdateCommentScene}
                onDeleteCommentScene={onDeleteCommentScene}
              />
            </TabPane>
            <TabPane
              tab={`Resolved (${listCommentSceneGroupByParentResolved.length})`}
              key="2"
            >
              <List
                list={listCommentSceneGroupByParentResolved}
                onMoveToCommentScene={onMoveToCommentScene}
                onSelectCommentScene={onSelectCommentScene}
                onUpdateCommentScene={onUpdateCommentScene}
                onDeleteCommentScene={onDeleteCommentScene}
              />
            </TabPane>
          </Tabs>
        </div>
      </ListCommentWrapper>
    );
  },
);

export default ListComment;
